@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@-webkit-keyframes modaldrop {
  from {
    -webkit-transform: translate(0px, 50px);
    transform: translate(0px, 50px);
  }

  to {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }
}

@keyframes modaldrop {
  from {
    -webkit-transform: translate(0px, 50px);
    transform: translate(0px, 50px);
  }

  to {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }
}

